import React, { useState, useEffect, useRef } from "react";
import { Input, Row, Col, Space, Tooltip } from "antd";
import { Icon } from "@iconify/react";
import {
  LikeOutlined,
  DislikeOutlined,
  LikeFilled,
  DislikeFilled,
} from "@ant-design/icons";
import "../../Styles/Custom.css";
import axios from "axios";
import refreshOutline from "@iconify/icons-basil/refresh-outline";

const Chat = ({ ques, act }) => {
  const API_BASE_URL1 = "http://4.193.34.131:8080";
  const API_BASE_URL2 = "http://4.193.34.131:8081";
  const lastMessageRef = useRef(null);
  const [data, setData] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [question, setQuestion] = useState("");
  const [likes, setLikes] = useState([]);
  const [dislikes, setDislikes] = useState([]);
  const [generates, setGenerates] = useState([]);
  const [forceFetch, setForceFetch] = useState(false);

  useEffect(() => {
    if (lastMessageRef.current) {
      lastMessageRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [data]);

  useEffect(() => {
    if (forceFetch) {
      fetchData();
      setForceFetch(false);
    }
  }, [question, forceFetch]);

  useEffect(() => {
    if (ques) {
      addNew(ques);
    }
  }, [ques]);

  const addNew = (question) => {
    setForceFetch(true);
    question = question.trim();
    if (question.length === 0) return;
    const tempData = [...data];
    const obj = {
      question: question,
      answer: "",
      isLoading: true,
      headers: [],
      rows: [],
    };

    setLikes((prevLikes) => [...prevLikes, false]);
    setDislikes((prevDislikes) => [...prevDislikes, false]);
    setGenerates((prevGenerates) => [...prevGenerates, false]);

    tempData.push(obj);
    setData([...data, obj]);
    setSearchText("");
    setQuestion(question);
  };
  const handleLike = async (index) => {
    setLikes((prevLikes) => [...prevLikes.slice(0, index), !prevLikes[index]]);
  };

  const handleDislike = async (index) => {
    setDislikes((prevDislikes) => [
      ...prevDislikes.slice(0, index),
      !prevDislikes[index],
    ]);
  };

  const handleGenerate = async (index, item) => {
    if (act === 1) {
      try {
        setData((prevData) => {
          const updatedData = [...prevData];
          updatedData[index].isLoading = true;
          return updatedData;
        });
        if (
          item?.question?.includes("chart") ||
          item?.question?.includes("graph") ||
          item?.question?.includes("Graph") ||
          item?.question?.includes("Chart")
        ) {
          const answer = await axios.get(
            `http://4.193.105.54:8080/receive_csv/?query=${item?.question}&data=true`
          );
          setData((prevData) => {
            const updatedData = [...prevData];
            updatedData[index].answer = answer?.data?.data?.answer?.response;
            updatedData[index].isLoading = false;
            return updatedData;
          });

          setLikes((prevLikes) => [...prevLikes.slice(0, index), false]);
          setDislikes((prevDislikes) => [
            ...prevDislikes.slice(0, index),
            false,
          ]);
        } else {
          const answer = await axios.get(
            `${API_BASE_URL1}/unstructured/askme/?query=${item?.question}&data=false`
          );
          if (answer?.data?.answer?.response?.includes("|")) {
            const rows = answer.data.answer.response
              .split("\n")
              .map((line) => line.trim())
              .filter(Boolean);

            const headings = rows[0]
              .split("|")
              .map((heading) => heading.trim())
              .filter(Boolean);

            const dataRows = rows.slice(2).map((row) => {
              const rowData = row
                .split("|")
                .map((cell) => cell.trim())
                .filter(Boolean);
              return rowData;
            });

            setData((prevData) => {
              const updatedData = [...prevData];
              updatedData[index].answer = answer?.data?.answer?.response;
              updatedData[index].isLoading = false;
              updatedData[index].headers = headings;
              updatedData[index].rows = dataRows;
              return updatedData;
            });
          }

          setData((prevData) => {
            const updatedData = [...prevData];
            updatedData[index].answer = answer?.data?.answer?.response;
            updatedData[index].isLoading = false;
            return updatedData;
          });

          setLikes((prevLikes) => [...prevLikes.slice(0, index), false]);
          setDislikes((prevDislikes) => [
            ...prevDislikes.slice(0, index),
            false,
          ]);
        }
      } catch (err) {
        console.log(err);
        setData((prevData) => {
          const updatedData = [...prevData];
          updatedData[index].isLoading = false;
          return updatedData;
        });
      }
    } else {
      try {
        setData((prevData) => {
          const updatedData = [...prevData];
          updatedData[index].isLoading = true;
          return updatedData;
        });
        if (
          item?.question?.includes("chart") ||
          item?.question?.includes("graph") ||
          item?.question?.includes("Graph") ||
          item?.question?.includes("Chart")
        ) {
          const answer = await axios.get(
            `http://4.193.105.54:8080/receive_csv/?query=${item?.question}&data=true`
          );
          setData((prevData) => {
            const updatedData = [...prevData];
            updatedData[index].answer = answer?.data?.data?.answer?.response;
            updatedData[index].isLoading = false;
            return updatedData;
          });

          setLikes((prevLikes) => [...prevLikes.slice(0, index), false]);
          setDislikes((prevDislikes) => [
            ...prevDislikes.slice(0, index),
            false,
          ]);
        } else {
          const answer = await axios.post(
            `${API_BASE_URL2}/query-pdf/?query=${question}`
          );
          if (answer?.data?.response?.includes("|")) {
            const rows = answer.data.response
              .split("\n")
              .map((line) => line.trim())
              .filter(Boolean);

            const headings = rows[0]
              .split("|")
              .map((heading) => heading.trim())
              .filter(Boolean);

            const dataRows = rows.slice(2).map((row) => {
              const rowData = row
                .split("|")
                .map((cell) => cell.trim())
                .filter(Boolean);
              return rowData;
            });

            setData((prevData) => {
              const updatedData = [...prevData];
              updatedData[index].answer = answer?.data?.response;
              updatedData[index].isLoading = false;
              updatedData[index].headers = headings;
              updatedData[index].rows = dataRows;
              return updatedData;
            });
          }

          setData((prevData) => {
            const updatedData = [...prevData];
            updatedData[index].answer = answer?.data?.response;
            updatedData[index].isLoading = false;
            return updatedData;
          });

          setLikes((prevLikes) => [...prevLikes.slice(0, index), false]);
          setDislikes((prevDislikes) => [
            ...prevDislikes.slice(0, index),
            false,
          ]);
        }
      } catch (err) {
        console.log(err);
        setData((prevData) => {
          const updatedData = [...prevData];
          updatedData[index].isLoading = false;
          return updatedData;
        });
      }
    }
  };
  async function fetchData() {
    if (act === 1) {
      try {
        const tempData = [...data];
        if (
          question.includes("chart") ||
          question.includes("graph") ||
          question.includes("Graph") ||
          question.includes("Chart")
        ) {
          const answer = await axios.get(
            `http://4.193.105.54:8080/receive_csv/?query=${question}&data=true`
          );
          const newData = [...tempData];
          newData[newData.length - 1].answer =
            answer?.data?.data?.answer?.response ||
            "Oops, something went wrong. Please try again later.";
          newData[newData.length - 1].isLoading = false;
          setData(newData);
        } else {
          const answer = await axios.get(
            `${API_BASE_URL1}/unstructured/askme/?query=${question}&data=false`
          );
          const newData = [...tempData];
          newData[newData.length - 1].answer =
            answer?.data?.answer?.response ||
            "Oops, something went wrong. Please try again later.";
          if (answer?.data?.answer?.response?.includes("|")) {
            const rows = answer.data.answer.response
              .split("\n")
              .map((line) => line.trim())
              .filter(Boolean);

            const headings = rows[0]
              .split("|")
              .map((heading) => heading.trim())
              .filter(Boolean);

            const dataRows = rows.slice(2).map((row) => {
              const rowData = row
                .split("|")
                .map((cell) => cell.trim())
                .filter(Boolean);
              return rowData;
            });
            newData[newData.length - 1].headers = headings;
            newData[newData.length - 1].rows = dataRows;
          }
          newData[newData.length - 1].isLoading = false;
          setData(newData);
        }
      } catch (e) {
        const tempData = [...data];
        const newData = [...tempData];
        if (newData.length > 0) {
          newData[newData.length - 1].answer =
            "Oops, something went wrong. Please try again later.";
          newData[newData.length - 1].isLoading = false;
          setData(newData);
          console.log(e);
        }
      }
    } else {
      try {
        const tempData = [...data];
        if (
          question.includes("chart") ||
          question.includes("graph") ||
          question.includes("Graph") ||
          question.includes("Chart")
        ) {
          const answer = await axios.get(
            `http://4.193.105.54:8080/receive_csv/?query=${question}&data=true`
          );
          const newData = [...tempData];
          newData[newData.length - 1].answer =
            answer?.data?.data?.answer?.response ||
            "Oops, something went wrong. Please try again later.";
          newData[newData.length - 1].isLoading = false;
          setData(newData);
        } else {
          const answer = await axios.post(
            `${API_BASE_URL2}/query-pdf/?query=${question}`
          );
          const newData = [...tempData];
          newData[newData.length - 1].answer =
            answer?.data?.response ||
            "Oops, something went wrong. Please try again later.";
          if (answer?.data?.response?.includes("|")) {
            const rows = answer.data.response
              .split("\n")
              .map((line) => line.trim())
              .filter(Boolean);

            const headings = rows[0]
              .split("|")
              .map((heading) => heading.trim())
              .filter(Boolean);

            const dataRows = rows.slice(2).map((row) => {
              const rowData = row
                .split("|")
                .map((cell) => cell.trim())
                .filter(Boolean);
              return rowData;
            });
            newData[newData.length - 1].headers = headings;
            newData[newData.length - 1].rows = dataRows;
          }
          newData[newData.length - 1].isLoading = false;
          setData(newData);
        }
      } catch (e) {
        const tempData = [...data];
        const newData = [...tempData];
        if (newData.length > 0) {
          newData[newData.length - 1].answer =
            "Oops, something went wrong. Please try again later.";
          newData[newData.length - 1].isLoading = false;
          setData(newData);
          console.log(e);
        }
      }
    }
  }
  return (
    <div className="chatcontainer">
      <Row gutter={32} style={{ marginTop: "50px" }}>
        <Col span={24}>
          <div className="botcontainer">
            {data.length === 0 && (
              <Row justify={"center"}>
                <Col>
                  <img
                    src="/Assets/background.svg"
                    alt="Centered"
                    style={{ marginBottom: "150px" }}
                  />
                </Col>
              </Row>
            )}

            {data.map((item, chatIndex) => (
              <div
                key={`chat${chatIndex}`}
                ref={chatIndex === data.length - 1 ? lastMessageRef : null}
              >
                <Row>
                  <Col span={12}> </Col>
                  <Col span={10}>
                    <div className="d-flex gap-3">
                      <div className="userchat">
                        <div className="textSmall">{item.question}</div>
                      </div>
                      <div className="userimg">
                        <img src="/Assets/user.svg" alt="user" />
                      </div>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col span={1}></Col>
                  <Col span={12}>
                    <div className="d-flex  gap-3 m-5">
                      <div className="botimg">
                        <img src="/Assets/bot.svg" alt="bot" />
                      </div>
                      <div className="botchat">
                        {item.isLoading && (
                          <Icon
                            icon="eos-icons:three-dots-loading"
                            color="#29256e"
                            fontSize={24}
                          />
                        )}
                        {!item.isLoading && (
                          <div
                            style={{
                              fontSize: 14,
                              fontWeight: 400,
                              color: "#204496",
                            }}
                          >
                            {typeof item.answer === "string" && (
                              <>
                                {item?.answer?.includes("|") ? (
                                  <table
                                    style={{
                                      borderCollapse: "collapse",
                                      width: "100%",
                                    }}
                                  >
                                    <thead>
                                      <tr>
                                        {item?.headers?.map(
                                          (heading, index) => (
                                            <th
                                              key={index}
                                              style={{
                                                border: "1px solid black",
                                                padding: "8px",
                                              }}
                                            >
                                              {heading}
                                            </th>
                                          )
                                        )}
                                      </tr>
                                    </thead>
                                    <tbody>
                                      {item?.rows?.map((rowData, rowIndex) => (
                                        <tr key={rowIndex}>
                                          {rowData.map(
                                            (cellData, cellIndex) => (
                                              <td
                                                key={cellIndex}
                                                style={{
                                                  border: "1px solid black",
                                                  padding: "8px",
                                                }}
                                              >
                                                {cellData}
                                              </td>
                                            )
                                          )}
                                        </tr>
                                      ))}
                                    </tbody>
                                  </table>
                                ) : item?.answer?.includes(
                                    "data:image/png;base64"
                                  ) ? (
                                  <>
                                    <img
                                      src={`${item.answer}`}
                                      alt=""
                                      style={{
                                        width: "100%",
                                      }}
                                    />
                                    <br />
                                  </>
                                ) : (
                                  <div
                                    dangerouslySetInnerHTML={{
                                      __html: item.answer
                                        .replace(/\n/g, "<br />")
                                        .replace(/- /g, "  "),
                                    }}
                                    className="white-space-pre-line"
                                  />
                                )}
                                <Space>
                                  <Tooltip title="Good Response">
                                    <span
                                      className="icon-wrapper"
                                      onClick={() => handleLike(chatIndex)}
                                      style={{
                                        pointerEvents: dislikes[chatIndex]
                                          ? "none"
                                          : "auto",
                                      }}
                                    >
                                      {likes[chatIndex] ? (
                                        <LikeFilled />
                                      ) : (
                                        <LikeOutlined />
                                      )}
                                    </span>
                                  </Tooltip>
                                  <Tooltip title="Bad Response">
                                    <span
                                      className="icon-wrapper"
                                      onClick={() => handleDislike(chatIndex)}
                                      style={{
                                        pointerEvents: likes[chatIndex]
                                          ? "none"
                                          : "auto",
                                        paddingTop: "10px",
                                      }}
                                    >
                                      {dislikes[chatIndex] ? (
                                        <DislikeFilled />
                                      ) : (
                                        <DislikeOutlined />
                                      )}
                                    </span>
                                  </Tooltip>
                                  <Tooltip title="Regenerate">
                                    <span
                                      className="icon-wrapper"
                                      onClick={() =>
                                        handleGenerate(chatIndex, item)
                                      }
                                    >
                                      {generates[chatIndex] ? (
                                        <Icon
                                          icon={refreshOutline}
                                          style={{
                                            fontSize: "28px",
                                          }}
                                        />
                                      ) : (
                                        <Icon
                                          icon={refreshOutline}
                                          style={{
                                            fontSize: "28px",
                                          }}
                                        />
                                      )}
                                    </span>
                                  </Tooltip>
                                </Space>
                              </>
                            )}
                          </div>
                        )}
                      </div>
                    </div>
                  </Col>
                </Row>
              </div>
            ))}

            <div
              className="d-flex gap-3 justify-content-center"
              style={{ marginBottom: "50px" }}
            >
              <Input
                className="chatinput"
                placeholder="Type your message ....."
                value={searchText}
                onChange={(e) => {
                  setSearchText(e.target.value);
                }}
                onPressEnter={() => addNew(searchText)}
              />
              <div className="sendbutton" onClick={() => addNew(searchText)}>
                <img src="/Assets/send.svg" alt="send" />
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default Chat;
