import React, { useState } from "react";
import { Row, Col } from "antd";
import Sider from "../Components/ChatBot/Sider";
import Chat from "../Components/ChatBot/Chat";

const Home = () => {
  const [question, setQuestion] = useState("");
  const [active , setActive] = useState(1);

  const handleQuestionFromSider = (question) => {
  
    setQuestion(question);
  };
  const handleActive = (active) =>{
    console.log(active);
    setActive(active);
  }
  return (
    <Row gutter={32}>
      <Col span={6}>
        <Sider handleQuestion={handleQuestionFromSider} handleActiveStatus={handleActive}/>
      </Col>
      <Col span={18}>
        <Chat ques={question} act={active}/>
      </Col>
    </Row>
  );
};

export default Home;
